  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  line-height: 1; }

body {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji !important; }

img {
  vertical-align: middle; }

ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

.Landing_Page {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;
  z-index: 10000; }
  .Landing_Page .layer {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    -webkit-transform-origin: top;
            transform-origin: top;
    -webkit-animation: open 1s ease-in-out forwards;
            animation: open 1s ease-in-out forwards; }
    .Landing_Page .layer:nth-child(1) {
      background: #0d0d0d;
      z-index: 20; }
    .Landing_Page .layer:nth-child(2) {
      background: #1a1a1a;
      -webkit-animation-delay: 1s;
              animation-delay: 1s;
      z-index: 19; }

@-webkit-keyframes open {
  0% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1); }
  100% {
    -webkit-transform: scaleY(0);
            transform: scaleY(0); } }

@keyframes open {
  0% {
    -webkit-transform: scaleY(1);
            transform: scaleY(1); }
  100% {
    -webkit-transform: scaleY(0);
            transform: scaleY(0); } }
