html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  line-height: 1; }

body {
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Helvetica Neue, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol, Noto Color Emoji !important; }

img {
  vertical-align: middle; }

ol,
ul {
  list-style: none;
  margin: 0;
  padding: 0; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

a {
  text-decoration: none; }

.LoginPicture {
  position: relative;
  height: 100vh;
  background-image: radial-gradient(rgba(0, 0, 0, 0.05), black), url(../assets/images/switzerland-4290226_1920.jpg);
  background-size: cover;
  background-position: center center;
  background-attachment: fixed;
  z-index: 1; }
  .LoginPicture p {
    font-size: 60px;
    color: white; }
    @media (max-width: 425px) {
      .LoginPicture p {
        font-size: 35px; } }
  .LoginPicture .fas {
    font-size: 40px; }
    @media (max-width: 425px) {
      .LoginPicture .fas {
        font-size: 70px; } }
  .LoginPicture .Community a {
    color: #333333;
    transition: color 0.5s; }
    .LoginPicture .Community a:hover {
      color: black; }
  .LoginPicture .Community .fab {
    font-size: 40px; }
  .LoginPicture .Anchor {
    color: white;
    position: absolute;
    bottom: 80px;
    right: 50%;
    max-width: 50px;
    margin-right: -25px;
    animation: fontbulger 1.5s infinite; }
    .LoginPicture .Anchor:hover {
      cursor: pointer; }

@keyframes fontbulger {
  0% {
    bottom: 50px; }
  30% {
    bottom: 100px; }
  100% {
    bottom: 50px; } }
